* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #223343;
}

header {
  padding: 50px 15px 0px;
}

header h1 {
  color: #FFF;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

main {
  width: 100%;
  max-width: 980px;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none; 
  outline: none;
  background: none;

  background-color: #EEE;
  border-radius: 8px;

  color: #53565A;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out
}

.searchbox:focus {
  box-shadow: 0px 0px 8px 3px #4484C4;
}